@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&subset=cyrillic,cyrillic-ext,latin-ext");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
}

.out {
  display: flex;
}

@media only screen and (max-width: 1023px) {
  .out {
    flex-direction: column;
  }
}

.main,
.aside {
  overflow: auto;
  height: 100vh;
}

@media only screen and (max-width: 1023px) {
  .main,
  .aside {
    height: auto;
  }
}

.main {
  flex-grow: 1;
  padding: 80px 0 40px;
  background-image: linear-gradient(to bottom, #47457e, #2e2d54);
}

@media only screen and (max-width: 1023px) {
  .main {
    padding: 40px 0;
  }
}

.inner {
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 845px;
}

.label {
  display: block;
  padding: 20px 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
  letter-spacing: 0.5px;
  color: #8697a8;
}

.title {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.33;
  text-align: center;
  color: #32325d;
}

.aside {
  width: 413px;
  padding: 40px;
}

@media only screen and (max-width: 1023px) {
  .aside {
    width: 100%;
    padding: 40px 20px;
  }
}

.aside__head {
  border-bottom: 1px solid #dddddd;
  padding: 0 0 24px;
  margin-bottom: 12px;
}

.pickup {
  padding: 24px;
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.pickup__row {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
}

.pickup__image {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.pickup__text {
  margin-left: 16px;
}

.pickup p {
  font-size: 13px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.85;
  text-align: left;
  color: #8996a6;
}

.pickup__title {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.71;
  letter-spacing: 1.2px;
  color: #32325d;
}

.service-address {
  height: 140px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
