body {
	background: #FFF;
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	font-size: 15px;
	line-height: 1.4; }

.out {
	display: flex;
	@include md {
		flex-direction: column; } }
.main,
.aside {
	overflow: auto;
	height: 100vh;
	@include md {
		height: auto; } }
.main {
	flex-grow: 1;
	padding: 80px 0 40px;
	@include gb($twilight, $dark-grey-blue);
	@include md {
		padding: 40px 0; } }
.inner {
	position: relative;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	max-width: 845px; }

.label {
	display: block;
	padding: 20px 0;
	font-size: 16px;
	font-weight: normal;
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $bluey-grey; }

.title {
	font-size: 18px;
	font-weight: 600;
	line-height: 1.33;
	text-align: center;
	color: $dark-grey-blue-two; }
